import { ClientType } from 'generated/prisma';
import { getDefaultPersonForm, convertPersonFormToDto, PersonForm } from './person.forms';
import {
    CompanyForm,
  convertCompanyFormToDto,
  getDefaultCompanyForm,
} from './company.forms';
import { CreateClientDto } from '../types/dtos/client.dto';

export type ClientForm =
  | { type: typeof ClientType.PERSON; data: PersonForm }
  | { type: typeof ClientType.COMPANY; data: CompanyForm };

export const getDefaultClientForm = (type: ClientType): ClientForm =>
  type === 'PERSON'
    ? {
        type: ClientType.PERSON,
        data: getDefaultPersonForm(),
      }
    : {
        type: ClientType.COMPANY,
        data: getDefaultCompanyForm(),
      };

export const convertClientFormToDto = (form: ClientForm): CreateClientDto => {
  if (form.type === ClientType.PERSON) {
    return {
      type: ClientType.PERSON,
      data: convertPersonFormToDto(form.data as PersonForm),
    };
  } else {
    return {
      type: ClientType.COMPANY,
      data: convertCompanyFormToDto(form.data as CompanyForm),
    };
  }
};
